.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#mainTitle {
  text-align: left;
  margin-left: 25.3px;
}

#leftArrow {
  float: left;
  width: 2%;
  height: 300px;
  padding: 100px 0;
}
#rightArrow {
  float: left;
  width: 2%;
  height: 300px;
  padding: 100px 0;
}
#listItem {
  float: left;
  width: 24%;
  text-align: left;
  font-family: "Avenir Next", sans-serif;
}

.photo-wrapper {
  overflow: hidden;
  /* transform-style: preserve-3d; */
  width: 90%;
  display: grid;
  grid-template-columns: 30px 200px 30px;
  grid-template-rows: 100px 100px 30px;
}

.images {
  position: absolute;
  width: 200px;
  height: 190px;
  z-index: 100;
}

.images.active {
  position: absolute;
  width: 250px;
  height: 190px;
  z-index: 900;
}

.metaData {
}

.category {
  font-size: 12px;
  color: #ff72b6;
  letter-spacing: 0.4px;
  text-transform: uppercase;
  font-weight: 600;
}

.price {
  font-size: 19px;
  line-height: 19px;
  font-weight: 600;
  margin: 10px 0 4px;
}

.details {
  font-size: 16px;
  font-weight: 400;
  margin-top: 0;
  margin-bottom: 4px;
}

.imageButtons {
  position: absolute;
  float: left;
  overflow: hidden;
  text-align: center;
  right: 0;
  left: 0;
  z-index: 2;
}

.item1 {
  grid-column: 1;
  grid-row: 1 / span 2;
  height: 100%;
  width: 100%;
  padding: 100px 0;
  z-index: 1000;
}
.item2 {
  grid-column: 2;
  grid-row: 1 / span 2;
}
.item3 {
  grid-column: 3;
  grid-row: 1 / span 2;
  height: 100%;
  width: 100%;
  padding: 100px 0;
  z-index: 1000;
}

.item4 {
  grid-column: 1 / span 3;
  grid-row: 3;
  text-align: center;
  z-index: 1000;

  overflow: hidden;
  margin: auto;
  width: 120px;
}

.imageButtons {
  padding: 0;
  display: flex;
  transition: all 0.2s;
  position: relative;
  margin: 0;
}

.buttons.active {
  background-color: black;
  border-radius: 50%;
  color: black;
}

button {
  background-color: transparent;
  border: none;
  border-radius: 50%;
  margin: 0;
}

button:before {
  margin: 0;
  background-color: #fff;
  display: inline-block;
  border-radius: 50%;
  opacity: 0.5;
  height: 6px;
  width: 6px;
  content: "";
  transition: transform 0.35s;
}
